export class AuthStorage {
  private static readonly TOKEN_KEY = '@token';

  public static readonly getAccessToken = async (): Promise<string | null> => {
    return localStorage.getItem(AuthStorage.TOKEN_KEY);
  };

  public static readonly setAccessToken = async (
    token: string,
  ): Promise<void> => {
    localStorage.setItem(AuthStorage.TOKEN_KEY, token);
  };

  public static readonly removeAccessToken = async (): Promise<void> => {
    localStorage.removeItem(AuthStorage.TOKEN_KEY);
  };
}
