// Import FirebaseAuth and firebase.
import firebase from 'firebase/compat/app';
import 'firebase/compat/auth';
import React, { FC, useState } from 'react';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';
import { LoadingIndicator } from '../components/LoadingIndicator';
import { config } from '../config/config';
import { AuthService } from '../data/services/auth.service';
import { getFirstAndLastName } from '../utils/getFirstAndLastName';

export const LoginScreen: FC = () => {
  const [loading, setLoading] = useState(false);

  const signUp = async (user: firebase.User) => {
    setLoading(true);
    try {
      const idToken = await user.getIdToken();

      const { firstName, lastName } = getFirstAndLastName(user?.displayName);

      await AuthService.auth({
        firebaseToken: idToken,
        email: user.email ?? undefined,
        firstName,
        lastName,
      });

      // TODO: Handle user state in context. Navigate to other screens.
      // TODO: Error handling.
    } catch (error) {
      // console.error('error', error);
    } finally {
      setLoading(false);
    }
  };

  const uiConfig: firebaseui.auth.Config = {
    callbacks: {
      signInSuccessWithAuthResult() {
        const user = firebase.auth().currentUser;

        if (!user) return false;

        signUp(user).finally(() => {
          return false;
        });

        return false;
      },
    },
    signInOptions: [firebase.auth.GoogleAuthProvider.PROVIDER_ID, 'apple.com'],
  };

  if (firebase.apps.length === 0) {
    firebase.initializeApp(config.firebase);
  }

  if (loading) return <div>Loading...</div>;

  return (
    <div>
      <h1>Multytude</h1>
      <p>Sign Up</p>
      {loading && <LoadingIndicator />}
      <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={firebase.auth()} />
    </div>
  );
};
